











































































































@import "@/scss/theme";

.skill {
  margin: auto 30px;
  color: $color-white;
  text-align: center;
  .skill-content {
    display: flex;
    text-align: left;
    max-width: 900px;
    padding-bottom: 50px;
    margin: 0 auto;
    .skill-card {
      flex: 1;
      margin: 15px;
      border-top: $color-intro-h3 2px solid;
      display: flex;
      flex-wrap: wrap;
      h2 {
        width: 100%;
      }
    }
  }
  @media only screen and (max-width: 650px) {
    .skill-content {
      flex-direction: column;
      text-align: center;
      .skill-card {
        display: block;
      }
    }
  }
}
p {
    margin: 0;
}
